import React ,{useState}from 'react';
function UserLogIn(props){
    const [isLoggedIn,setIsLoggedIn]=useState(false);
    return(
        <div className="login-signup-form">
            <form action="#" onSubmit={props.handleUserLogIn}>
                <input type="email" placeholder="Enter email" className="input-form" name="email"/>
                <input type="password" placeholder="Enter password" className="input-form"name="password"/>
                <button className="btn">Log In</button>
            </form>
        </div>
    )
}
export default UserLogIn;