import React ,{useState,useEffect} from 'react';
import {createStore} from 'redux';
import NavBar from './NavBar';
import LogIn from './LogIn'
import UserSignUp from './UserSignUp';
import UserLogIn from './UserLogIn';
import UserProfile from './UserProfile';
import * as firebase from 'firebase';
import states from '../reducers/index';
import {setUserData} from '../actions/index';
function App() {
  const [user,setUser]=useState('notLoogged');
  const [logInCheck,setLogInCheck]=useState('userLogInForm');
  const store=createStore(states);
  // useEffect(()=>{
  //   console.log("yes")
  // })
  function handleUserLogIn(e){
    e.preventDefault();
    const email=e.target.email.value;
    const password=e.target.password.value;
    if(email.length===0 || password.length===0){
        alert("Please dont submit empty credential");
        return;
    }

    firebase.firestore().collection("resoluteData").where("email", "==", email)
    .get()
    .then((snapshot)=> {
        snapshot.docs.map((user)=>{
            if(user.data().password===password){
              const data=user.data();
              data.userId=user.id;
              sets(data);
              setLogInCheck('userLoggedIn');
            }else{
              alert("please check your email or passweor");
            }
        })
    })
    .catch((error)=> {
        console.log("Error getting documents: ", error);
    });
}
const sets=(data)=>{
  setUser(data)
}
const handleSignOut=()=>{
  setLogInCheck('userLogInForm')
}
  console.log('store',store.getState());
  return (
    <div className="App">
      <NavBar 
              logInCheck={logInCheck} 
              handleSignOut={handleSignOut}
              />
      {logInCheck==='userLogInForm' && <LogIn handleUserLogIn={handleUserLogIn}/>}
      {logInCheck==='userLoggedIn' && <UserProfile data={user}/>}  
    </div>
  );
}

export default App;
