import React from 'react';
function NavBar(props){
    console.log("nav",props);
    return(
        <div className="navbar">
            {props.logInCheck==='userLoggedIn' &&  <div style={styles.admin} onClick={props.handleSignOut}>Sign Out</div>}
        </div>
    )
}
const styles={
    admin:{
        color:'white',
        float:'right',
        border:'1px solid lightgrey',
        borderRadius:10,
        fontSize:16,
        padding:5,
        margin:15,
        cursor:'pointer',
        marginRight:30
    }
}
export default NavBar;