import React,{useState} from 'react';
function UserProfile(props){
    const [openCamera,setOpenCamera]=useState(false);
    function addImage(){
        setOpenCamera(true);
    }
    function hideCamera(){
        setOpenCamera(false);
    }
      console.log("abt",props);
    return(
        <div className="profile-container">
            <div className="user-details">Name :    {props.data.name}</div>
            <div className="user-details">E-mail :  {props.data.email}</div>
            <div className="user-details">Gender :  {props.data.gender}</div>
            <div className="user-details">Hometown :    {props.data.hometown}</div>
        </div>
    )
}
export default UserProfile;