import React,{useState}from 'react';
import * as firebase from 'firebase';
function UserSignUp(){
    const [userExists,setuserExists]=useState(false);
    const signUp=(e)=>{
        e.preventDefault();
        const name=e.target.name.value;
        const gender=e.target.gender.value;
        const email=e.target.email.value;
        const hometown=e.target.hometown.value;
        const password=e.target.password.value;
        const confirm=e.target.confirmPassword.value;
        if(name.length===0 || gender.length===0 || email.length===0 || hometown.length===0 || password.length===0 || confirm.length===0 ){
            alert("Please dont submit empty credential");
            return;
        }
        if(password!==confirm){
            console.log("incorect");
            document.getElementsByTagName('form')[0].reset();
            alert(" Please check your password");
            return;
        }
        firebase
            .firestore()
            .collection('resoluteData')
            .add({
                name:name,
                gender:gender,
                email:email,
                hometown:hometown,
                password:password
            })
            .then((docRef)=>{
                alert("Successfully sign up please log in")
            }).catch((err)=>{
                console.log("error");
            })
    }
    
    return (
        <div className="login-signup-form">
            <form action="#" onSubmit={signUp}>
            <input type="text" placeholder="Enter Full name" className="input-form" name="name" />
            <div style={{marginTop:-15,marginBottom:10}}>
                <h5>Gender</h5>
                <div style={{display:'flex',justifyContent:'space-around'}}>
                    <input type="radio" name="gender" value="male"/>Male
                    <input type="radio" name="gender" value="female"/>Female
                    <input type="radio" name="gender" value="other"/>Other
                </div>
            </div>
            <input type="email" placeholder="Enter email" className="input-form" name="email"/>
            <input type="text" placeholder="Enter Hometown" className="input-form" name="hometown"/>
            <input type="password" placeholder="Enter password" className="input-form" name="password"/>
            <input type="password" placeholder="Confirm password" className="input-form" name="confirmPassword"/>
            <button className="btn">Sign Up</button>
            </form>
        </div>
    )
}
export default UserSignUp;