import React from 'react';
import UserLogIn from './UserLogIn';
import UserSignUp from './UserSignUp';
class LogIn extends React.Component{
    constructor(){
        super();
        this.state={
            logInSignInStatus:true
        }       
    }
    handleLogIn=()=>{
        document.getElementById('signup').style.backgroundColor='gainsboro';
        document.getElementById('login').style.backgroundColor='chocolate';
        this.setState({
            logInSignInStatus:true
        })
    }
    handleSignUp=()=>{
        document.getElementById('login').style.backgroundColor='gainsboro';
        document.getElementById('signup').style.backgroundColor='chocolate';
        this.setState({
            logInSignInStatus:false
        })
    }
    render(){
        const {logInSignInStatus}=this.state;
        return(
            <div className="login-signup-container">
                <div style={styles.logInSignup}>
                    <div style={styles.buttonLogInSignUP} id="login" onClick={this.handleLogIn}>Log In </div>
                    <div style={styles.buttonLogInSignUP} id="signup" onClick={this.handleSignUp}>Sign Up</div>
                </div>
                {logInSignInStatus && <UserLogIn handleUserLogIn={this.props.handleUserLogIn}/>}
                {!logInSignInStatus && <UserSignUp />}
            </div>
        )
    }
}
const styles={
    logInSignup:{
        display:'flex',
        height:50,
        width:'90%',
        borderRadius:10,
        justifyContent:'space-around',
        margin:'auto',
        marginTop:20,
    },
    buttonLogInSignUP:{
        border:'1px solid gainsboro',
        borderRadius:10,
        padding:10,
        width:100,
        marginTop:10,
        paddingLeft:50,
        cursor:'pointer'
    }
}
export default LogIn;